import React from 'react';
import './style.css';

export default function TransactionHistory(props){
    return (
        <table className="transaction-history">
        <thead>
            <tr>
            <th>Type</th>
            <th>Amount</th>
            <th>Currency</th>
            </tr>
        </thead>
        <tbody>
        {props.transactions.map(({id,type,amount,currency})=>
        <tr key={id}>
            <td>{type}</td>
            <td>{amount}</td>
            <td>{currency}</td>
        </tr> )}
        </tbody>
        </table>
    );
};